

.Module ul{
 
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #2975f0;
    
}
.Module li{

    float: left;
}
.Module a{
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;

}

.Module a:active
  
{
    background-color: #111;
    color: rgb(100, 74, 247);


}

.Module a:hover
  
{
    background-color:#99b3de;
    color: blue;


}

.Welcome{
text-align: right;
font-style: italic;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
color: rgb(67, 6, 180);


}


