
.mianDiv{
    width: 37%;
    margin: 20px auto;
    border: 1px solid white;
    -webkit-box-shadow: 0 2px 3px #ccc;
            box-shadow: 0 2px 3px #ccc;
    text-align: left;

}
.EnableFlow {

    border-radius: 5px;
    background-color: white;
    padding: 20px ;
    width: auto ;
}
.col25 {
    float: left;
    width: 50%;
    margin-top: 6px;
  }
  
  .col75 {
    float: left;
    width: 45%;
    margin-top: 6px;
 
  }

  .ButtonDiv{

    text-align: center;  
    padding: 10px ;
  }


  .row:after {
    content: "";
    display: table;
    clear: both;
  }

.EnableFlow a {
    text-decoration: none;
    font-size: 1em;
    color:black;
}
 
.EnableFlow a:visited {
    color: black;
}
@media screen and (max-width: 600px) {
    .col25, .col75, input[type=submit] {
      width: 100%;
      margin-top: 0;
    }
}


table {
  border-collapse: collapse;
  width: 100%;
  text-align: center;

}



